<template>
  <div class="lp_page">
    <div class="lp_switch">
      <el-switch
        v-model="onlyIn"
        inactive-color="#BECBD2"
        active-color="#56C9C2"
      ></el-switch>
      只看已投
    </div>

    <div class="hot_lp_item" v-for="(value, index) in tokens" :key="index" v-show="value.invested || !onlyIn">
      <div class="new"></div>
      <el-row type="flex" justify="space-between">
        <el-col :span="18" class="title">{{ value.token | tokenName}} Mdex LP</el-col>
        <el-col :span="6" class="col_right">
          <div class="icon"></div>
        </el-col>
      </el-row>
      <el-row type="flex" justify="space-between" class="row">
      <el-col :span="12" class="col_left">年化收益：</el-col>
      <el-col :span="12" class="col_right">
        216%
        <div class="icon icon_small"></div>
        <div class="icon icon_small"></div>
      </el-col>
    </el-row>
   
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">总锁仓量：</el-col>
        <el-col :span="12" class="col_right">
          {{ value.total }}
        </el-col>
      </el-row>

       <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">已存：</el-col>
        <el-col :span="12" class="col_right">{{ value.myPledge }}</el-col>
      </el-row>
     
     
      <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">我的总收益：</el-col>
        <el-col :span="12" class="col_right">{{ value.myIncome }} HUSKY</el-col>
      </el-row>

       <el-row type="flex" justify="space-between" class="row">
        <el-col :span="12" class="col_left">当前收益:</el-col>
        <el-col :span="12" class="col_right">{{ value.canWithdraw }} HUSKY</el-col>
      </el-row>

      <el-row type="flex" :gutter="20" justify="space-between">
        <el-col>
          <div class="button button_border" @click="onWithDraw(index,value.myPledge)">撤回</div>
        </el-col>
        <el-col>
          <!-- <div class="button button_border" @click="deposit(index)">质押</div> -->
          <div class="button button_border"  @click="showPop(index,value.token)">质押</div>
        </el-col>
      </el-row>

      <div class="button button_fill" @click="approve(value.token,value.address)" v-if="value.isAllow">授权 {{ value.token | tokenName}} Mdex LP</div>
      <div  class="button button_fill" @click="onWithDraw(index,1)">领取收益</div>



    <el-row type="flex" justify="space-between">
      <el-col :span="16" class="can_use_left">{{value.token | tokenName}} Mdex LP可用：</el-col>
      <el-col :span="8" class="can_use_right">{{ value.balance }}</el-col>
    </el-row>

      <el-row type="flex" justify="end">
        <el-col :span="24" class="to_get" @click="onToMdex">获得HOT-HBTC Mdex LP > </el-col>
      </el-row>
    </div>
    <!--列表结束-->

   <div class="shadow" v-show="vpop">
       

      <div class="my_form">
        <div class="title">质押 {{currentCoin}} <button @click="handleClose">关闭</button></div>
        <div class="input_box">
          <input type="number"  v-model="amount" />
          <span>{{currentCoin}}</span>
          <span class="btn_all"  @click="all">全部</span><!--全部-->
        </div>

        <div class="balance">
         余额：
          <span class="red">{{popBalance}} {{currentCoin}}</span>
        </div><!--质押-->

      

		<div class="sub_btn">
			
			<button class="common_btn" @click="deposit(index)">质押</button>
		</div>

      </div>
   </div>

  </div>
</template>

<script>
import Net from "@/assets/net.js";
var mintContract = new web3.eth.Contract(Net.LPMintabi, Net.LPContact);

export default {
  data() {
    return {
      drawer: false,
      direction: 'btt',
      amount:"",
      currentCoin:"",
      popBalance:"--",
      index:0,
      tokens: [
        {
          pid: 0,
          token: "HOT_HOG_LP",
          total: "--",
          output:"--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:true,
          address:Net.HOT_HOG_LP,
          isAllow:false,
          canWithdraw:"--",
        },
        {
          pid: 1,
          token: "HUSD_HOG_LP",
          total: "--",
          output: "--",
          totalIncome: 12,
          myPledge: "--",
          myIncome: "--",
          balance: "--",
          invested:true,
          address:Net.HUSD_HOG_LP,
          isAllow:false,
          canWithdraw:"--",
        }
      ],
      onlyIn: false,
      showPool:true,
      address:"",
      vpop:false,
    };
  },
   mounted() {
    var _this =this;
    _this.$getAddress();  
    setTimeout(function () {

     _this.isAllowance("HOT_HOG_LP",0); //ok
     _this.isAllowance("HUSD_HOG_LP",1);

     _this.getUserInfo(0);
     _this.getUserInfo(1);

     _this.getBalance("HOT_HOG_LP",0); //ok
     _this.getBalance("HUSD_HOG_LP",1)



     _this.lookCanWithdraw(0);
     _this.lookCanWithdraw(1);


  
     _this.numByPerDay("HOT_HOG_LP",0);
     _this.numByPerDay("HUSD_HOG_LP",1)

     
     _this.totalPledge("HOT_HOG_LP",0);
     _this.totalPledge("HUSD_HOG_LP",1);

    }, 1000)
    

  },
  filters:{
    tokenName:(value=> value == "HOT_HOG_LP" ? "HOT-HOG" : "HUSD-HOG")
  },
  methods:{
    onWithDraw(index,amount){
       //提币或撤回
      var _this = this;
      var value=new BigNumber(amount).multipliedBy(BigNumber(10).pow(18));
      console.log("index:"+index,"value:"+value)
      mintContract.methods
        .withdraw(index,value)
        .send({ from: _this.address })
        .then((res)=>{
           _this.getUserInfo(index)
        })
    },
    showDraw(){
      this.drawer = true
    },
    onToMdex(){

    },
    all(){
      this.amount = this.popBalance
    },
    handleClose () {
      this.vpop = false;
      this.index = "";
      this.currentCoin = "";
      this.popBalance =""
    },
    showPop(index,token){
      var _this = this;
      this.vpop = true;
      this.index = index;
      this.currentCoin = token;
      this.popBalance =_this.tokens[index].balance

      console.log("index:"+this.index+"balance:"+this.popBalance)
    },
    deposit(index) {  //质押 OK
     
      var _this = this;
     

      var value=new BigNumber(_this.amount).multipliedBy(BigNumber(10).pow(18));
      
      console.log("index:"+index,"value:"+value)
      mintContract.methods
        .deposit(index,value)
        .send({ from: _this.address })
        .then((res)=>{
           _this.getUserInfo(index)
           _this.vpop = false;
        })
    },
    approve(token,address) { //授权OK
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.LPTokenabi, address);
      var value=new BigNumber(10000000).multipliedBy(BigNumber(10).pow(18));

      currentToken.methods
        .approve(Net.LPContact, value)
        .send({from:_this.address})
        .then(function (res) {
          
        });
    },
    isAllowance(token,index) { //查询是否授权 OK
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.LPTokenabi, Net[token]);
      console.log(_this.address,Net.LPContact)
      currentToken.methods
        .allowance(_this.address,Net.LPContact)
        .call({from:_this.address})
        .then(function (res) { 
            res > 0?_this.tokens[index].isAllow = false : _this.tokens[index].isAllow = true
            console.log(token+"授权数量："+res /1e18)
        });
    },
    getUserInfo(index){ //我的质押和收益 OK
      var _this = this;
      mintContract.methods
        .userInfo(index,_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].myPledge= res.amount / 1e18
          _this.tokens[index].myIncome =res.rewardDebt  / 1e18
           console.log(index+"池子的信息："+JSON.stringify(res))
        });
    },
    getBalance(token,index){ //获取余额 OK
      var _this = this;
      var currentToken= new web3.eth.Contract(Net.LPTokenabi, Net[token]);

      currentToken.methods
        .balanceOf(_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].balance = res / 1e18
           console.log(token+"余额："+res / 1e18)
        });
    },
    totalPledge(token,index){ //获取总质押 OK
      var _this = this;
      console.log("总质押地址"+Net[token])
      console.log("LPTokenabi:"+JSON.stringify(Net.LPTokenabi))
      console.log("LPMintabi:"+JSON.stringify(Net.LPMintabi))

      var currentToken= new web3.eth.Contract(Net.LPTokenabi, Net[token]);
      currentToken.methods
        .balanceOf(Net.LPMintabi)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].total = res / 1e18;
           console.log(token+"总质押："+res / 1e18)
        });
    },
    lookCanWithdraw(index){ //查看可提取
      var _this = this;
     
       mintContract.methods
        .pendingHusky(index,_this.address)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].canWithdraw = res / 1e18;
           console.log(index+"池可提取："+res / 1e18)
        });
    },

    numByPerDay(token,index){ //日产量
      var _this = this;
       mintContract.methods
        .numByPerDay(index)
        .call({from:_this.address})
        .then(function (res) {
          _this.tokens[index].output = res / 1e18;
           console.log(index+"日产量："+res / 1e18)
        });
    },
  }
};
</script>

<style lang="scss" scoped>


</style>


